<template>
  <section id="pix">
    <h2>PIX</h2>
    <div id="featured">
      <img
        class="featured"
        v-bind:src="featured">
    </div>
    <ol id="thumbnails">
      <li 
        v-for="(pic, index) in pix"
        :key="index">
        <img 
          class="thumbnail" 
          v-bind:src="pic.src"
          @click="setFeatured(index)">
      </li>
    </ol>
  </section>
</template>

<script>
export default {
  name: "Pix",
  methods: {
    setFeatured(index) {
      this.featured = `pix/${index}.jpg`;
    }
  },
  data() {
    return {
      featured: "pix/0.jpg",
      pix: [
        { src: "pix/0.jpg" },
        { src: "pix/1.jpg" },
        { src: "pix/2.jpg" },
        { src: "pix/3.jpg" },
        { src: "pix/4.jpg" },
        { src: "pix/5.jpg" },
        { src: "pix/6.jpg" },
        { src: "pix/7.jpg" }
      ]
    };
  }
};
</script>

<style scoped>
#pix {
}

ol {
  list-style-type: none;
}

#featured {
  height: 30rem;
}

.featured {
  height: 100%;
}

#thumbnails li {
  display: inline-block;
}

.thumbnail {
  margin: 0 0.25rem;
  display: inline-block;
  height: 4em;
}
.thumbnail:hover {
  cursor: pointer;
}
</style>
